* {
  box-sizing: border-box;
  margin: 0;
}

.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  left: 0;
  right: 0;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

.logo__item {
  width: 40px;
  height: 40px;
  display: block;
}

.nav ul {
  padding: 0;
}

.nav__list {
  margin: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  list-style: none;
  transition: color .3s;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link.is-active {
  color: #00acdc;
  margin-left: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__product-name {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  display: flex;
}

.card:hover .card__product-name {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  width: 100%;
  margin-bottom: 16px;
}

.card__review {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews-count {
  padding-top: 1px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-cost {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  padding-block: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  margin-bottom: 22px;
  --columns: 1;
  grid-template-columns: repeat(var(--columns), 1fr);
  justify-content: center;
  gap: 46px 48px;
  max-width: 1024px;
  margin-inline: auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --columns: 2;
  }
}

@media (width >= 768px) {
  .main {
    --columns: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --columns: 4;
  }
}

